import axios from 'axios'
import {API_ROOT,API_KEY} from "../config";

export default function (url,params) {
    return new Promise((resolve,reject)=>{
        axios(API_ROOT+url,{
            params,
            headers:{
                apikey:API_KEY
            }
        }).then(res=>{
            let {err,data,msg} = res.data;
            resolve(data);
        },err=>{
            alert('Fail');
            reject(err);
        })
    })
}