<template>
    <div class="hot_link">
        <h2>电影活动 &nbsp;·&nbsp;·&nbsp;·&nbsp;·&nbsp;·&nbsp;·</h2>
        <ul>
            <li v-for="item in list">
                <a :href="item.url" target="_blank">
                    {{item.title}}
                </a>
            </li>
        </ul>
    </div>

</template>

<script>
    import axios from 'axios'
    export default {
        name: "hot_link",
        data(){
            return{
                list: []
            }
        },
        async created(){
            this.list = await this.$axios('/hotlinks');
        },
    }
</script>

